import { clsx } from 'clsx';
import { FunctionalIconNames } from '../icon';
import { FunctionalIcon } from '../icon/Icon';
import { LinkMode, LinkProps, LinkSize, LinkTarget } from './link.interface';

export const Link: React.FC<LinkProps> = ({
  as: Tag = 'a',
  size = LinkSize.Base,
  mode = LinkMode.Inline,
  disabled = false,
  target = LinkTarget.Self,
  label,
  icon = 'arrow-long-right',
  iconPlacement,
  ...props
}) => {
  return (
    <Tag
      className={clsx(
        'zep-typography-bodyText',
        'zep-outline-none',
        'zep-rounded-4',
        'zep-border-separate',
        'zep-leading-1.25',
        'zep-inline',
        'zep-items-center',
        'zep-cursor-pointer',
        'zep-underline-offset-2',
        { 'zep-flex zep-items-center': mode !== LinkMode.Inline },
        { 'zep-link-inline': mode === LinkMode.Inline },
        { 'zep-link-standalone': mode === LinkMode.Standalone },
        { 'active:zep-text-neutral-dark-active': !disabled },
        { 'zep-underline': mode === LinkMode.Inline },
        { 'hover:zep-underline': mode === LinkMode.Standalone && !disabled },
        { 'hover:zep-cursor-default !zep-text-neutral-dark-disabled/40': disabled },
        { 'zep-text-0.875': size !== LinkSize.Base },
        { 'zep-ring-focus focus-visible:zep-ring': Tag === 'a' },
      )}
      tabIndex={disabled ? -1 : 0}
      disabled={disabled}
      aria-disabled={disabled}
      target={target}
      {...props}
    >
      <span className={clsx('zep-inline', { 'hover:zep-cursor-default': disabled })}>{label}</span>

      {icon && (
        <FunctionalIcon
          name={icon as FunctionalIconNames}
          size={20}
          className={clsx(
            'zep-inline',
            { 'zep-ml-0.25': label && iconPlacement === 'after' },
            iconPlacement === 'before' ? '-zep-order-1 zep-mr-0.25' : '',
          )}
        />
      )}
    </Tag>
  );
};
